*{
  color: white;
  background-color: black;
}


#home {
  width: 80%;
  height: 70vh;
  margin: auto;
  display: flex;
  /* background-color: #f2e8ca; */
  
}
#rightimg > img{
  width: 80%;
  margin: auto;

}

.text-color{
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 26px;
  font-style: italic;
  font-family: cursive;
}
#home > div {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#home  img {
  box-shadow: black;
  height: 50%;
  border-radius: 8px;
  background-image: #1C2541;
  background-size: 200% 200%;
  border-radius: 4%;
}

.name { 
  color: white;
  margin-top:10px;

}

.name > div{
  width: 80%;
  height: 40px;
}


