.name button {
  background-color: black;
  background-size: 200% 200%;
  color:white;
  box-shadow: black;
  padding: 21px;
  font-size: 24px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  border-radius: 4%;
  box-shadow: black;
  transition: 0.5s;
}

#resume-button-2:hover {
  background-color: turquoise;
  color: black;
}

.contlink:hover{
  background-color: turquoise;
  color: black;
}

.name > a:hover{
    transition: 0.5s;
    cursor: pointer;
    background-color: white;
    color: #173313;
}

.download{
  color:white;
  font-size: 25px;
}
.name button:hover {
  background-color: black;
  background-size: 200% 200%;
}

