.portButton{
  display: flex;
  align-self:center;
  align-items: center;
  height: 60px;
}

#ResButton{
  height: 70px;
}

#ResButton > button {
  margin: auto;

}

.contactlink {
  display: flex;
  width: 90%;
  justify-content:space-around;
  align-content: center;
  height: 80px;
  
}

.contactlink > div {
  width: 30%;
  
}
.contlink{
  width: 100%;
  height: 40px;
}





@media only screen and (max-width: 900px) {
  #home {
    flex-direction: column-reverse;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .name h1 {
    margin: 10px 0;
    font-size: 18px;
    width: 100%;
  }

  .name {
    width: 100%;
    margin-top: 10px;
  }

  #home img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: bottom;
  }

  
}


@media only screen and (min-width:340px) and (max-width:480px){
  .name{
    margin-top: 10px;
  }
}