*{
  color: white;
  background-color: black;
}


.navbar {
  width: 100%;
  height: 70px;
  box-shadow: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 100px;
  position: fixed;
  z-index: 1;
  box-sizing: border-box;
  color: white;
  font-family: 'Work Sans', sans-serif;
  background-color: black;
  /* border: 5px solid red; */
  border-bottom: 1px solid white;
}



.navbar ul {
  display: flex;
  list-style-type: none;
  text-decoration: none;
  /* border: 5px solid red; */
}
.navbar ul li {
  margin: 20px;
  color: white;
  font-size: 18px;
  cursor: pointer;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}
.resumee {
  margin: 20px;
  color: white;
  font-size: 18px;
  cursor: pointer;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}

.resumee:hover {
  background-color: aqua;
  width: 90px;
  height: 23px;
}

.navbar ul li:hover {
color: white
}

.nav-link:hover {
  background-color: aqua;
  width: 80px;
  height: 23px;
}





